<template>
<div class="app-page order-detail">
    <page-content class="order-detail-content">
      <div class="shipping vertical-center">
        <div class="img">
          <img src="static/images/category/makesureorder/dw.png"/>
        </div>
        <div class="shipping-info">
          <div class="receiver">
            <span class="name">杨二</span>
            <span class="phone">18569366969</span>
          </div>
          <div class="address">
            <p class="font-ellipsis">北京 北京市-海淀区-北清路高级技工仪器仪表学院东仪器仪表学院东仪器仪表学仪器仪表学院东仪器仪表学仪器仪表学院东仪器仪表学仪器仪表学院东仪器仪表学院东</p>
          </div>
        </div>
      </div>
      <div class="product vertical-center">
        <div class="product-inner">
          <div class="food-img">
            <img src="static/images/category/jz.png"/>
          </div>
          <div class="food-detail">
            <div class="food-name">
              <span>新鲜紫茄子 绿色食品</span>
            </div>
            <div class="food-specification">
              <span>规格:&nbsp;5kg</span>
            </div>
            <div class="food-price">
              <span>￥: 30.9</span>
            </div>
          </div>
          <div class="food-status">
            <div class="buy-status">
              退款中
            </div>
            <div class="food-num">
              x2
            </div>
          </div>
        </div>
      </div>
      <div class="logistics-information">
        <title-second title="物流信息" :hideRight="true" size="small"></title-second>
        <div class="logistics-detail">
          <div class="logistics-item" :class="{'logistics-item-active': index === 0}" :key="index" v-for="(item, index) in 3">
            <van-icon class="icon-passed" name="passed" size=".53rem"/>
            <page-line direction="V" borderStyle="dashed" :length="1" :dashedSpacing="0.1"></page-line>
            <div class="logistics-item-left">订单正在派件中</div>
            <div class="logistics-item-right">01-18</div>
          </div>
        </div>
      </div>
      <div class="order-information">
        <title-second title="订单信息" :hideRight="true" size="small"></title-second>
        <div class="order-detail">
          <div class="detail-1">备注：未填写</div>
          <div class="detail-1">下单时间：2019-01-14</div>
          <div class="detail-1">付款方式：微信</div>
          <div class="detail-1">订单编号：154263264562546</div>
        </div>
      </div>
    </page-content>
</div>

</template>
<script>
import PageContent from '@/components/page/content'
import TitleSecond from '@/components/page/title'
import PageLine from '@/components/page/line'
export default {
  name: 'OrderDetail',
  components: { PageContent, TitleSecond, PageLine },
  data () {
    return {}
  },
  created () {
  },
  mounted () {
  },
  computed: {
  },
  methods: {},
  watch: {}
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .order-detail{
    font-family: $font-family-Medium;
    .order-detail-content{
      padding: 10px 0;
      .shipping{
        width: 100%;height: 120px;
        padding: 0 30px;
        .img{
          width: 34px;height: 42px;
          img{width: 100%;height: 100%;}
        }
        .shipping-info{
          margin-left: 20px;
          /*flex: 1;*/
          width: calc(100% - 60px);
          display: flex;flex-direction: column;justify-content: center;
          .receiver{
            .name{
              font-size: 30px;
              font-family: $font-family-Medium;
              color: $black333333;
            }
            .phone{
              margin-left: 20px;
              font-size: 20px;
              font-family: $font-family-Medium;
              color: $black999999;
            }
          }
          .address{
            margin-top: 10px;
            width: 100%;
            p{
              width: 100%;
              font-family: $font-family-Medium;
              font-size: 24px;
              color: $black333333;
            }
          }
        }
      }
      .product{
        height: 210px;
        background-color: #fafafa;
        padding: 0 30px;
        .product-inner{
          width: 100%;
          height: 150px;
          display: flex;
          .food-img{
            width: 150px;
            height: 150px;
            img{ width:150px;height: 100% }
          }
          .food-detail{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: calc(100% - 250px);
            height: 90%;
            padding-left: 15px;
            .food-name{
              font-size: 29px;
              font-weight: 700;
              font-family: $font-family-Bold;
            }
            .food-specification{
              font-size: 20px;
              color: $black999999;
            }
            .food-price{
              font-size: 29px;
              font-family: $font-family-Regular;
              color: $redffdc8251d;
            }
          }
          .food-status{
            width: 100px;height: 100%;
            display: flex;flex-direction: column;justify-content: space-between;align-items: flex-end;
            .buy-status{
              font-size: 26px;color: $green22ac38;
            }
            .food-num{
              font-size: 26px;color: $black666666;
              font-family: $font-family-Regular;
            }
          }
        }
      }
      .logistics-information{
        width: 100%;
        padding: 0 30px;
        /*height: 290px;*/
        margin-top: 20px;
        display: flex;flex-direction: column;justify-content: space-between;
        .logistics-detail{
          margin-top: 30px;
          .logistics-item{
            position: relative;
            display: flex;justify-content: space-between;
            font-size: 28px;color: $blackcccccc;
            font-family: $font-family-Medium;
            margin-bottom: 47px;
            &-active{
              color: $black666666;
              font-family: $font-family-Bold;
              font-weight: 500;
            }
            >.icon-passed{
              position: absolute;
              left: 0.09rem;top: calc(50% - 0.25rem);
              background-color: $whiteffffff;
              z-index: 20;
            }
            >.line{
              position: absolute;
              left: 0.333rem;top: -0.35rem;
              z-index: 10;
            }
            .logistics-item-left{
              margin-left: 66px;
            }
          }
        }
      }
      .order-information{
        width: 100%;
        height: 230px;
        padding: 0 30px;
        margin-top: 35px;
        display: flex;flex-direction: column;justify-content: space-between;
        font-family: $font-family-Regular;
        .order-detail{
          width: 100%;height: 160px;
          display: flex;flex-direction: column;justify-content: space-between;
          color: #666666;font-size: 23px;
        }
      }
    }
  }
</style>



